@media (min-width:769px) and (max-width:969px) {
    .signIn {
        width: 450px;
        height: 100%;
        align-items: baseline;
        position: fixed;
    }

    .signIn .inner {
        width: 380px;
        margin-top: 50px;
        height: 500px;
    }

    .signIn .inner .middle {
        height: 300px;
    }

    .signIn .inner .middle .middleInner {
        height: 280px;
    }

    .signIn .inner .middle .middleInner form label {
        margin-right: 10px;
        line-height: 25px;
        font-size: 14px;
    }

    .signIn .inner .middle .middleInner form>div:first-child>input,
    .signIn .inner .middle .middleInner form>div:nth-child(3)>input {
        height: 25px;
        width: 95%;
        padding-left: 5px;
    }

    .signIn .inner .middle .middleInner form .idWrap,
    .signIn .inner .middle .middleInner form .passWrap {
        height: auto;
    }

    .signIn .inner .middle .middleInner .iderror,
    .signIn .inner .middle .middleInner .passerror {
        margin: 15px 0 0px 0px;
        font-size: 14px;
        height: 20px;
    }

    .signIn .inner .middle .middleInner form .remem {
        height: 30px;
        margin-top: 10px;
    }

    .signIn .inner .middle .middleInner form>div:nth-child(5)>input {
        height: 15px;
        width: 15px;
    }

    .signIn .inner .middle .middleInner form .loginWrap {
        height: 30px;
    }

    .signIn .inner .middle .middleInner form .loginWrap .loginRight button {
        width: 80px;
        height: 25px;
        font-size: 14px;
        margin-right: 10px;
    }
}

@media (max-width:768px) {
    .signIn {
        width: 250px;
        height: 100vh;
        align-items: baseline;
        position: fixed;
    }

    .signIn .inner {
        width: 90%;
        margin-top: 30px;
        height: 500px;
    }

    .signIn .inner .top {
        margin-bottom: 10px;
    }

    .signIn .inner .middle {
        height: 330px;
    }

    .signIn .inner .middle .middleInner {
        height: 330px;
        display: flex;
        justify-content: center;
    }

    .signIn .inner .middle .middleInner form {
        margin-left: 0;
        width: 85%;
    }

    .signIn .inner .middle .middleInner form label {
        margin-right: 10px;
        line-height: 25px;
        font-size: 14px;
    }

    .signIn .inner .middle .middleInner form>div:first-child>input,
    .signIn .inner .middle .middleInner form>div:nth-child(3)>input {
        height: 25px;
        width: 100%;
        padding-left: 5px;
    }

    .signIn .inner .middle .middleInner form .idWrap,
    .signIn .inner .middle .middleInner form .passWrap {
        height: auto;
    }

    .signIn .inner .middle .middleInner .iderror,
    .signIn .inner .middle .middleInner .passerror {
        margin: 5px 0 10px 0px;
        font-size: 10px;
        height: 20px;
    }

    .signIn .inner .middle .middleInner form .remem {
        height: 30px;
        margin-top: 10px;
    }

    .signIn .inner .middle .middleInner form>div:nth-child(5)>input {
        height: 15px;
        width: 15px;
    }

    .signIn .inner .middle .middleInner form .loginWrap {
        height: 100px;
        display: flex;
        align-items: baseline;
        flex-direction: column;
    }

    /* .signIn .inner .middle .middleInner form .loginWrap .loginRight input{
    width:120px;
    height:25px;
    font-size: 14px;
    margin-right: 0px;
} */
    .signIn .inner .middle .middleInner form .loginWrap .loginLeft {
        width: 50%;
        margin: 10px 0 20px 0;
    }

    .signIn .inner .middle .middleInner form .loginWrap .loginRight {
        width: 100%;
        display: flex;
        justify-content: center;
    }

    .signIn .inner .middle .middleInner form .loginWrap .loginRight button {
        background: linear-gradient(to bottom, #87e0fd 0%, #53cbf1 40%, #05abe0 100%);
        width: 120px;
        height: 30px;
    }
}