* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

html body {
  font-family: 'Noto Sans TC', sans-serif;
  letter-spacing: 1px;
  overflow-x: hidden;
}

html h1,
html h2 {
  font-family: Helvetica, Arial;
  font-weight: 700;
  letter-spacing: 1px;
}

#wrapper {
  width: 100%;
  overflow: hidden;
  background-color: #fefbf4;
  position: relative;
}

#wrapper .md {
  width: 100%;
  height: 100%;
  right: -100%;
  opacity: 0;
  position: absolute;
  z-index: 888;
}

.pantsWrapper {
  width: 100%;
  overflow: hidden;
  background-color: #fefbf4;
}