#nav{max-width:1400px;
    padding-bottom: 20px;
    margin: 0 auto;
    position: relative;
}
#nav .rwdMenu{
    position: absolute;
    top: 0;
    right: 0;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content:center;
    z-index: 999;
    opacity: 0;
}
#nav .rwdMenu>span{
    display: block;
    width: 30px;
    height: 2px;
    background-color: rgb(112, 112, 112);
    margin: 5px 0;
    transition: 0.3s all ease-in-out;
    transform:translateY(0px) rotate(0deg);
}
#nav .menuWrap {
    height: 100px;
}
#nav .menuWrap ul{
    position: absolute;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    left: 0px;
    transition: 0.3s all ease-in-out;
    z-index: 899;
}
#nav .menuWrap ul li{
    width:20%;
    height: 50px;
    margin-top:40px;
}
#nav .menuWrap ul li a{
    display:block;
    text-align:center;
    height:50px;
    width: 140px;
    margin: 0 auto;
    line-height:40px;
    color:#333;
    font-size:24px;
    font-weight:100;
    font-family: 'Poiret One', cursive;
    transition:0.3s all ease-in-out;
    border-bottom:1px solid #fefbf4;
}
#nav .menuWrap ul a:hover{
    width: 200px;
    transition:0.3s all ease-in-out;
    color: rgb(190, 190, 190);
}
#nav .menuWrap ul .aa a:hover{
    border-bottom:1px solid #0CF;
}
#nav .menuWrap ul .bb a:hover{
    border-bottom:1px solid #903;
}
#nav .menuWrap ul .cc a:hover{
    border-bottom:1px solid #F90;
}
#nav .menuWrap ul .dd a:hover{
    border-bottom:1px solid #6FC;
}
#nav .menuWrap ul .sign a:hover{
    border-bottom:1px solid rgb(44, 44, 44);
}
#nav .navMd{
    position: fixed;
    right: -100%;
    width: 100%;
    z-index: 987;
    height: 100vh;
}
#nav .menuWrap ul  .bb{
    position: relative;
}
#nav .menuWrap ul  .bb ul{
    position: absolute;
    /* display: flex; */
    flex-direction: column;
    background-color:rgb( 255,255,255,0.4);
}
#nav .menuWrap ul  .bb ul li{
    margin: 5px 0;
    text-align: center;
    width: 100%;
    cursor: pointer;
    display: none;
}
#nav .menuWrap ul  .bb:hover ul li{
    display: flex;
}
#nav .menuWrap ul  .bbUl {
    display: none;
}
  /*-----------------logo-----------------------*/
.logoWrap{
    max-width: 1400px;
    padding-bottom: 0px;
    display: flex;
    justify-content: center;
}	
.logo{
    background-repeat:no-repeat;
    width:350px;
    height:175px;
    background-position: center;
    }
.logo a{
    display:block;
    width:350px;
    height:175px;
    text-indent:-9999px;
}
.logo h1{
    display:block;
    width:300px;
    height:175px;
    text-indent:-9999px;
}
.logo_line{
    width: 35%;
    height:1px;
    background-color:#666;
    margin-top: 100px;
}

@media (min-width:970px) and (max-width:1169px){
    #nav{
        width:100%;
    }
    #nav .menuWrap ul li{
        width:20%;
    }
    #nav .menuWrap ul li a{
        width: 130px;
    }
    #nav .menuWrap ul a:hover{
        width: 160px;
    }
      /*-----------------logo-----------------------*/
    .logoWrap{
        width: 100%;
        padding-bottom: 0px;
    }	
    .logo{
        width:300px;
        height:175px;
        background-size: 280px auto;
        }
    .logo a{
        width:250px;
        height:175px;
    }
    .logo_line{
        width: 33%;
        margin-top: 100px;
    }
}

