.pantsWrapper {
    width: 100%;
    overflow: hidden;
    background-color: #fefbf4;
}

.pantsproWrap {
    width: 1400px;
    padding-bottom: 100px;
    margin: 0 auto;
}

.circleBox {
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    margin: 200px 0;
}

.pantsproWrap .top {
    width: 100%;
    display: flex;
    /* height: 450px; */
    padding: 0 0 100px 0;
}

.pantsproWrap .top .left {
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
}

.pantsproWrap .top .left .smallPic {
    width: 16%;
}

.pantsproWrap .top .left .smallPic ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.pantsproWrap .top .left .smallPic ul li {
    width: 80px;
    height: 80px;
    margin: 0 0 10px 0;
    overflow: hidden;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid #fff;
}

.pantsproWrap .top .left .smallPic ul li img {
    height: 90%;
}

.pantsproWrap .top .left .bigPic {
    width: 60%;
    background-color: rgb(255, 255, 255);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 450px;
    overflow: hidden;
}

.pantsproWrap .top .left .bigPic img {
    width: auto;
    height: 80%;
}

.pantsproWrap .top .left .shareTo {
    width: 80%;
    height: 80px;
    display: flex;
    align-items: center;
    padding-left: 7%;
}

.pantsproWrap .top .left .shareTo span {
    font-size: 14px;
    color: #8a8a8a
}

.pantsproWrap .top .left .shareTo ul {
    display: flex;
    flex-wrap: wrap;
}

.pantsproWrap .top .left .shareTo ul li {
    width: 40px;
    height: 40px;
    margin: 0 3px;
}

.pantsproWrap .top .left .shareTo ul li a {
    display: block;
    border-radius: 50%;
}

.pantsproWrap .top .left .shareTo ul li img {
    width: 40px;
    height: 40px;
}

.pantsproWrap .top .right {
    width: 50%;
    padding: 0 0 0 40px;
}

.pantsproWrap .top .right .toCartContent {
    margin: 20px 0 0 0;
    padding: 0 0 0 10px;
    width: 80%;
}

.pantsproWrap .top .right .toCartContent .title h3 {
    font-size: 24px;
    font-weight: 900;
    margin: 50px 0 0px 0;
    line-height: 30px;
}

.pantsproWrap .top .right .toCartContent .title p {
    font-size: 16px;
    margin: 50px 0 20px 0;
    padding: 5px 0 5px 7px;
    border-left: 4px solid rgb(204, 35, 35);
    color: rgb(61, 61, 61);
}

.pantsproWrap .top .right .toCartContent .title span {
    font-size: 20px;
    font-weight: 900;
    margin: 10px 0 30px 0;
    color: rgb(204, 35, 35);
    display: block;
}

.pantsproWrap .top .right .toCartContent .title .rate {
    height: 30px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.pantsproWrap .top .right .toCartContent .title .rate p {
    font-size: 16px;
    margin: 0;
    padding: 0px 0 0px 7px;
    border-left: 2px solid rgb(204, 35, 35);
    color: rgb(61, 61, 61);
}

.pantsproWrap .top .right .toCartContent .title .rate ul {
    display: flex;
    flex-wrap: wrap;
    width: 150px;
}

.pantsproWrap .top .right .toCartContent .title .rate ul li {
    margin-right: 5px;
}

.pantsproWrap .top .right .toCartContent .title .rate ul li img {
    widows: 20px;
    height: 20px;
}

.pantsproWrap .top .right .toCartContent .number {
    width: 100%;
    height: 250px;
}

.pantsproWrap .top .right .toCartContent .number form {
    display: flex;
    flex-direction: column;
}

/* .pantsproWrap .top .right .toCartContent .number label{
    font-size: 14px;
    color:rgb(61, 61, 61);
    margin: 20px 0 5px 0;
}
.pantsproWrap .top .right .toCartContent .number select,input{
    height: 30px;
    width: 90%;
}
.pantsproWrap .top .right .toCartContent .number .numinp{
    padding-left: 5px;
} */
.pantsproWrap .top .right .toCartContent .number form .formWrap {
    width: 90%;
    margin-top: 40px;

}

.pantsproWrap .top .right .toCartContent .number .conform {
    width: 100%;
    height: 50px;
    display: flex;
    flex-wrap: wrap;
    margin: 40px 0 0 0;
}

.pantsproWrap .top .right .toCartContent .number .conform input {
    width: 43%;
    height: 40px;
    color: #fff;
    outline: none;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.2s all ease-in-out;
    /* 取消預設樣式 */
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
}

.pantsproWrap .top .right .toCartContent .number .conform input:first-child {
    background-color: #c74060;
    margin: 0 10px 0 0;
    border: 1px solid #c74060;
}

.pantsproWrap .top .right .toCartContent .number .conform input:first-child:hover {
    background-color: #ffffff;
    border: 1px solid #c74060;
    color: #c74060;
}

.pantsproWrap .top .right .toCartContent .number .conform input:last-child {
    background-color: #00E3E3;
    border: 1px solid #00E3E3;
}

.pantsproWrap .top .right .toCartContent .number .conform input:last-child:hover {
    background-color: #ffffff;
    border: 1px solid #00E3E3;
    color: #00E3E3;
}

.pantsproWrap .middle {
    width: 100%;
}

.pantsproWrap .middle .middleTop {
    width: 100%;
    height: 60px;
    border-bottom: 1px solid #cccccc;
}

.pantsproWrap .middle .middleTop ul {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.pantsproWrap .middle .middleTop ul li {
    width: 25%;
    display: flex;
    justify-content: center;
}

.pantsproWrap .middle .middleTop ul li a {
    display: block;
    text-align: center;
    line-height: 56px;
    width: 200px;
    transition: 0.3s all ease-in-out;
    border-bottom: 3px solid #fefbf4;
    cursor: pointer;
    color: #c0c0c0;
}

/* .pantsproWrap .middle .middleTop ul li:hover a{
    border-bottom: 3px solid #c74060;
    color:#c0c0c0;
} */
.pantsproWrap .middle .middleTop ul li .active {
    color: #5f5f5f;
    border-bottom: 3px solid #c74060;
}

.pantsproWrap .middle .middleBottom {
    width: 100%;
    height: auto;
    padding: 100px 0;
    overflow: hidden;
}

.pantsproWrap .middle .middleBottom .middlePos {
    width: 100%;
    margin-bottom: 100px;
    overflow: hidden;
}

.pantsproWrap .middle .middleBottom .middlePos ul {
    width: 300%;
    display: flex;
    flex-direction: row;
}

.pantsproWrap .middle .middleBottom .middlePos ul li {
    width: 100%;
    /* background-color: rgb(211, 107, 107); */
    text-align: center;
    display: flex;
    justify-content: center;
}

.pantsproWrap .middle .middleBottom .middlePos ul li img {
    width: 750px;
    height: auto;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:nth-child(2) ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:nth-child(2) ul li {
    width: 40%;
    display: inline-block;
}

.h3Font {
    font-size: 24px;
    line-height: 50px;
    font-weight: 700;
    color: rgb(92, 92, 92);
}

.pantsproWrap .middle .middleBottom .middlePos ul li:nth-child(2) ul li p {
    font-size: 16px;
    line-height: 24px;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child {
    display: flex;
    flex-wrap: wrap;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul {
    width: 100%;
    height: 250px;
    margin-top: 30px;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:first-child {
    width: 25%;
    display: flex;
    align-items: center;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) {
    width: 55%;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:last-child {
    width: 20%;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:first-child .commentWrap {
    width: 100%;
    height: 60px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:first-child .commentWrap h3 {
    font-size: 20px;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:first-child .commentWrap ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    margin: 0;
    height: 50px;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:first-child .commentWrap ul li {
    width: 30px;
    height: 30px;
    margin: 0 5px;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) .titleWrap {
    display: flex;
    flex-direction: column;
    /* align-items:flex-start; */
    margin: 0;
    width: 10%;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) .titleWrap li {
    margin: 3px 0;
    width: 80px;
    height: 20px;
    /* padding-right: 30px; */
    /* background-color: rgb(126, 79, 79); */
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) .scoreWrap {
    display: flex;
    flex-direction: column;
    margin: 0;
    width: 90%;

}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) .scoreWrap li {
    margin: 3px 0;
    width: 100%;
    height: 20px;
    display: flex;
    justify-content: flex-start;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) .scoreWrap li span {
    width: 5%;
    height: 15px;
    border-radius: 50px;
    background-color: rgb(247, 144, 144);
    margin-right: 20px;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) .scoreWrap li:first-child span {
    width: 90%;
}

.pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:last-child .storeWrap {
    width: 80%;
    margin-top: 55px;
}





@media (max-width:768px) {
    .pantsproWrap {
        width: 100%;
        padding-bottom: 100px;
    }

    .pantsproWrap .top {
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        /* height: 450px; */
        padding: 50px 0 30px 0;
    }

    .pantsproWrap .top .left {
        width: 95%;
        height: auto;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
    }

    .pantsproWrap .top .left .smallPic {
        width: 25%;
    }

    .pantsproWrap .top .left .smallPic ul li {
        width: 80px;
        height: 80px;
        margin: 0 0 8px 0;
    }

    .pantsproWrap .top .left .smallPic ul li img {
        height: 90%;
    }

    .pantsproWrap .top .left .bigPic {
        width: 75%;
        height: 400px;
    }

    .pantsproWrap .top .left .bigPic img {
        width: auto;
        height: 80%;
    }

    .pantsproWrap .top .left .shareTo {
        padding-left: 1%;
    }

    .pantsproWrap .top .right {
        width: 100%;
        padding: 0 0 0 0%;
    }

    .pantsproWrap .top .right .toCartContent {
        padding: 0 0 0 15px;
        width: 95%;
    }

    .pantsproWrap .top .right .toCartContent .title h3 {
        font-size: 20px;
        margin: 10px 0 0px 0;
        line-height: 26px;
    }

    .pantsproWrap .top .right .toCartContent .title p {
        font-size: 14px;
        margin: 10px 0 10px 0;
    }

    .pantsproWrap .top .right .toCartContent .title span {
        font-size: 32px;
        margin: 30px 0 20px 0;
    }

    .pantsproWrap .top .right .toCartContent .number {
        width: 100%;
        height: 250px;
    }

    /* .pantsproWrap .top .right .toCartContent .number select,input{
        width: 100%;
    } */
    .pantsproWrap .top .right .toCartContent .number form .formWrap {
        width: 100%;
        margin-top: 40px;

    }

    .pantsproWrap .top .right .toCartContent .number .conform {
        justify-content: center;
    }

    .pantsproWrap .top .right .toCartContent .number .conform input {
        font-size: 18px;
        /* 取消預設樣式 */
        appearance: none;
        -moz-appearance: none;
        -webkit-appearance: none;
    }

    .pantsproWrap .middle {
        width: 100%;
    }

    .pantsproWrap .middle .middleTop {
        width: 95%;
        height: 40px;
        margin: 0 auto;
    }

    .pantsproWrap .middle .middleTop ul {
        width: 100%;
    }

    .pantsproWrap .middle .middleTop ul li {
        width: 26%;
        margin: 0 10px;
    }

    .pantsproWrap .middle .middleTop ul li a {
        line-height: 36px;
        font-size: 12px;
        width: 100px;
    }

    /* .pantsproWrap .middle .middleTop ul li:hover a{
        border-bottom: 3px solid #c74060;
        color:#c0c0c0;
    } */
    .pantsproWrap .middle .middleBottom {
        width: 100%;
        height: auto;
        padding: 50px 0;
        overflow: hidden;
    }

    .pantsproWrap .middle .middleBottom .middlePos {
        width: 95%;
        margin-bottom: 0px;
        margin: 0 auto;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li img {
        width: 95%;
        height: auto;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:nth-child(2) ul li {
        width: 40%;
        display: inline-block;
        margin: 0 5px;
    }

    .h3Font {
        font-size: 16px;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:nth-child(2) ul li p {
        font-size: 12px;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul {
        width: 95%;
        height: auto;
        margin-top: 0px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:first-child {
        width: 100%;
        display: flex;
        align-items: center;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) {
        width: 100%;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:last-child {
        width: 100%;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:first-child .commentWrap {
        height: 80px;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        margin-bottom: 30px;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:first-child .commentWrap h3 {
        font-size: 16px;
        height: 20px;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:first-child .commentWrap ul {
        justify-content: flex-start;
        height: 30px;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:first-child .commentWrap ul li {
        width: 20px;
        height: 20px;
        margin: 0 5px 0 0;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) .titleWrap {
        display: flex;
        flex-direction: column;
        /* align-items:flex-start; */
        margin: 0;
        width: 10%;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) .titleWrap li {
        margin: 3px 0;
        width: 100%;
        height: 20px;
        display: flex;
        justify-content: flex-start;
        align-items: center;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) .scoreWrap {
        display: flex;
        flex-direction: column;
        margin: 0;
        width: 90%;

    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) .scoreWrap li {
        margin: 3px 0;
        width: 100%;
        height: 20px;
        display: flex;
        align-items: center;
        justify-content: flex-start;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) .scoreWrap li span {
        height: 10px;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:nth-child(2) .scoreWrap li:first-child span {
        width: 90%;
    }

    .pantsproWrap .middle .middleBottom .middlePos ul li:last-child ul li:last-child .storeWrap {
        width: 100%;
        margin-top: 25px;
    }


}