* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
}

html body {
    font-family: 'Noto Sans', sans-serif;
    letter-spacing: 1px;
    overflow-x: hidden;
}

html h1,
html h2 {
    font-family: Helvetica, Arial;
    font-weight: 700;
    letter-spacing: 1px;
}

.signinDataWrap {
    width: 100%;
    background-color: #fefbf4;
}

.dataWrap {
    max-width: 1400px;
    margin: 0 auto;
    padding: 50px 0 100px 0;
}

.dataWrap .datainnerWrap {
    width: 96%;
    margin: 0 auto;
    /* display: flex;
    flex-wrap: wrap;
    justify-content: center; */
    border: 1px solid rgb(214, 214, 214);
}

.CircuWrap {
    width: 100%;
    height: 100px;
    margin-top: 100px;
    display: flex;
    justify-content: center;
}

.detotalWrap {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    /* margin: 0 auto; */
}

.dataWrap .logoutWrap {
    width: 100%;
    margin: 0px 0 40px 0px;
    padding: 0 0 0 40px;
}

.dataWrap .logoutWrap .logout {
    width: 100px;
    height: 40px;
    line-height: 38px;
    text-align: center;
    border: 1px solid rgb(214, 214, 214);
    border-radius: 10px;
    color: #5c5c5c;
    cursor: pointer;
    /* appearance: unset;
    background-color: unset; */
}

.dataWrap .datainnerWrap .id1ul {
    width: 100%;
    display: flex;
    height: 50px;
}

.dataWrap .datainnerWrap .id1ul li {
    /* width: 14.2857142857%; */
    width: 100%;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    transition: 0.3s all ease-in-out;
    border-bottom: 1px solid #fefbf4;
}

.inactive {
    /* border-left: 1px solid rgb(214, 214, 214); */
    border-bottom: 1px solid rgb(214, 214, 214);
    background-color: #e9e6de;
    color: #7a7a7a;
}

