/* information */
.dataWrap .datainnerWrap .id_1 .left .info {
    width: 100%;
    height: 150px;
    padding: 0 30px 0 0;
    margin-top: 10px;
}

.dataWrap .datainnerWrap .id_1 .left .info .top_1 {
    height: 40px;
    display: flex;
    align-items: center;
}

.dataWrap .datainnerWrap .id_1 .left .info .top_1 h3 {
    padding: 0 10px;
    font-size: 20px;
    font-weight: 900;
    color: rgb(75, 75, 75);
}

.dataWrap .datainnerWrap .id_1 .left .info .top_1 span {
    width: 120px;
    height: 28px;
    background-color: rgb(255, 182, 72);
